@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');

:root {
    /* global variables. */
    font-size: 10px;
    --bg-color: rgb(255, 255, 246);
    --canvas-color: rgba(255, 255, 255, 1);
    --outline-color:rgba(44, 44, 44, 0.3);
    --alpha: 0.9;
}


* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

body {
    background-color: var(--bg-color);
    margin: 0;
}

.canvas--container {
    width: 100%;
    height: 80vh;
    background-color: var(--bg-color);
    padding: 40px 40px 0px 40px;
    filter: 10px;
}

canvas {
    width: 100%;
    height: 100%;
    background-color: var(--canvas-color);
    opacity: var(--alpha);
}

canvas:hover {
    cursor: crosshair;
}

.canvas--row {
    margin-top: 1rem;
    height: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    opacity: var(--alpha);
}

.button-set--color {
    display: flex;
    width: 400rem;
    max-width: 50%;
    height: 100%;
}


.button--color {
    width: 100%;
    height: 100%;
}


.button--clean {
    width: 4rem;
    height: 100%;
    display: flex;
    background-color: var(--canvas-color);
    box-shadow: 0 0px 5px 0px rgb(106, 106, 106);
    border-radius: 0.2rem;
    justify-content: center;
    align-items: center;
}

.button--clean:hover,
.button--color:hover {
    cursor: pointer;
    box-shadow: 
        inset 0 0px 5px 1px rgb(255, 255, 255),
        0 0px 5px 1px rgb(255, 255, 255);
    z-index: 100;
}